import React from 'react';
import '@fortawesome/fontawesome-free/css/all.min.css';


const Footer = () => {
  return (
    <footer className="bg-dark text-white text-center py-4">
      <div className="container">
        <div className="row">
          <div className="col-md-6">
            <h5>Follow Us On:</h5>
            <div className="d-flex justify-content-center">
              {/* Replace these Font Awesome icons with actual social media logos */}
              <a href="#" className="me-3 text-white">
                <i className="fab fa-facebook-f" style={{ fontSize: '24px' }}></i>
              </a>
              <a href="#" className="me-3 text-white">
                <i className="fab fa-twitter" style={{ fontSize: '24px' }}></i>
              </a>
              <a href="#" className="me-3 text-white">
                <i className="fab fa-instagram" style={{ fontSize: '24px' }}></i>
              </a>
              <a href="#" className="me-3 text-white">
                <i className="fab fa-linkedin" style={{ fontSize: '24px' }}></i>
              </a>
            </div>
          </div>
          <div className="col-md-6">
            <h5>Contact Us:</h5>
            <p>Mobile: +1234567890</p>
            <p>Email: example@example.com</p>
            <p>Demo Address: 123 Street, City, Country</p>
          </div>
        </div>
      </div>
    </footer>
  );
};

export default Footer;
